.header {
  color: #ffffff;
  box-sizing: border-box;
  font-family: 'Rubik';
  font-weight: 400;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 2;
}

.header-title {
  background-color: #099d8d;
  font-size: 1rem;
  font-style: normal;
  margin: 0rem;
  padding: 0.9375rem 0rem;
  top: 0rem;
  opacity: 0.90;
  line-height: 1.88;
}

.header-nav {
  background-color: #099d8d;
  opacity: 0.90;
  line-height: 2;
  font-weight: 500;
  font-size: 0.9375rem;
}

.header-nav-list {
  padding-inline-start: 0px;
  display: flex;
  justify-content: space-evenly;
  background-color: #099d8d;
  flex-wrap: wrap;
  align-items: center;
  padding: 0rem 0rem;
  margin: 0.5rem 0rem 0.4375rem;
 /* font-stretch: normal; */;
}

.header-nav-item {
  display: inline;
}

.header-nav-item-team,.header-nav-item-about-us,.header-nav-item-contact {
  color: #ffffff;
}

