  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.diamond {
  width: 1.875rem;
  height: 1.875rem;
  background: #ffffff;
  transform: rotate(45deg);
  justify-self: center;
  position: relative;
  bottom: 1.875rem;
  left: 48%;
}

.strength-weakness-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.strength {
  margin: 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.weakness {
  margin: 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.strength-weakness-title {
  box-sizing: border-box;
 /* height: 2.813rem */
  margin: 0rem;
  text-align: center;
  font-family: "Rubik";
  font-weight: bold;
  font-size: 1.375rem;
  line-height: 2.813rem;
  color: #099d8d;
}

.strength-weakness-text {
  margin-inline-start: 0.9375rem;
  margin-inline-end: 0.9375rem;
  display: flex;
