  font-size: 1rem;
  line-height: 1.5rem;
  color: #54585a;
}/* fin fortalezas y debilidades */.about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-basis: 100%;
}

.about-us-title {
  font-family: 'Rubik';
  font-size: 1.5rem;
  text-align: center;
  color: #099d8d;
  width: 100%;
  align-items: center;
}

.adalabers-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.img-container {
  text-align: center;
  width: 45%;
  height: 45%;
  margin: 0rem;
  padding: 0rem;
}

.adalaber-img {
  width: 100%;
  border-radius: 4px;
  border: solid 5px rgba(0, 0, 0, 0.08);
}

.information-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0rem;
  padding: 0rem;
  width: 90%;
}

.adalabers-name {
  font-family: 'OpenSans';
  font-size: 1.25rem;
  text-align: center;
  width: 100%;
  margin-bottom: 0rem;
}

.adalaber-text {
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: 'OpenSans';
  color: #54585a;
  text-align: center;
  margin: 1rem 0;
  width: 100%;
}

.about-us-icons {
 /* transform: scale(0.7); */
  margin-bottom: 2rem;
  color: #099d8d;
  line-height: 1.875rem;
  margin: 0rem;
}

.aside-list {
  margin-block-start: 0rem;
  margin-block-end: 0rem;
  padding: 0rem;
  margin-bottom: 2.5rem;
}

.aside-list a {
  font-size: 150%;
}

.about-us-icons-item {
  display: inline-block;
  margin: 0 1rem 0 0;
  list-style: none;
  padding-left: 2rem;
}

.about-us-icons-item a {
  text-decoration: none;
}

.fab {
  color: #099d8d;
}

.fas {
  color: #099d8d;
}

.footer {
  width: 100vw;
  background-color: #ffffff;
  display: block;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
  font-family: OpenSans;
  margin-top: 0;
  bottom: 0;
}

.footer-copy {
  font-size: 0.875rem;
  display: block;
  align-content: center;
  align-items: center;
  line-height: 1.25rem;
  color: #b8b8b9;
  text-align: center;
}

.footer-menu {
  box-sizing: border-box;
  margin: 0;
  font-size: 0.875rem;
}

.footer-menu-ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-inline-start: 0px;
  margin: 0;
}

.footer-menu-li {
  box-sizing: border-box;
  display: inline;
}

.footer-link-about-us,.footer-link-team,.footer-link-contact {
  opacity: 0.75;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.25rem;
  letter-spacing: normal;
  text-align: center;
  color: #54585a;
}

.footer-logo {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-adalab {
  height: 45px;
  width: 115.9px;
  margin: 30px;
  font-weight: bold;
  display: block;
  text-align: center;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

